var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('article',{staticClass:"event-anounce"},[_c('ul',[_c('li',[_vm._v("Показ: 29 февраля в 20:00")]),_c('li',[_vm._v("Длительность: 90 минут")]),_c('li',[_vm._v("Билет: 1000 рублей")]),_c('li',[_vm._v("Место: Клуб Blanc")])]),_c('p',[_vm._v("Дамы и господа, добро пожаловать на Аукцион. Мы приготовили для вас шоу страстей, которое проведет вас по пути алчности и очищения. Мы не торгуем вещами. Материя не интересует нас. Мы специализируемся на душевных переживаниях. На нашем Аукционе вы можете приобрести чужой опыт. То, что никогда не испытывали сами. И то, что останется с вами навсегда. 5 молодых людей — 5 откровенных историй. Воспоминания невинности, болотная эйфория, чувство спасения, привкус греха и отмщения. Все это может стать вашим.")]),_c('p',[_vm._v("50 зрителей получают 50 конвертов со случайно распределенной наличностью. Во время Аукциона вы можете докупать наличность, обменивая ее на настоящие деньги в пределах разумного. Выиграв опыт, вы покидаете шоу, и отправляетесь по пути приобретенного переживания. Тем, кому не повезло, оказываются заинтересованными наблюдателями и тоже увлекательно проведут время. В цену билета включен фуршет.")]),_c('p',[_vm._v("Участники: Теат ОС + Ася Ашман, Михаил Левин, ВасяРАН, Ромео Кастеллучи, Эрик Клэптон")])])}]

